export interface ProcessingLocation {
  id: string;
  name: string;
  product: string;
  targetQuality: number;
  unit: string;
  minQuality: number;
  maxQuality: number;
  color: string;
  description: string;
}

export interface QualitySample {
  id: string;
  locationId: string;
  sampleId: string;
  timestamp: Date;
  qualityValue: number;
  status: 'PASS' | 'FAIL' | 'WARNING';
  operator: string;
  notes?: string;
  batchNumber?: string;
  testMethod?: string;
}

export interface LocationData {
  location: ProcessingLocation;
  samples: QualitySample[];
  currentAverage: number;
  passRate: number;
  lastUpdated: Date;
}

