"use client";

import { QualitySample, PROCESSING_LOCATIONS } from "@/types/smelter";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";

interface DataTableProps {
  data: QualitySample[];
}

export function DataTable({ data }: DataTableProps) {
  return (
    <div className="rounded-md border">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead>Sample ID</TableHead>
            <TableHead>Location</TableHead>
            <TableHead>Product</TableHead>
            <TableHead>Quality Value</TableHead>
            <TableHead>Status</TableHead>
            <TableHead>Operator</TableHead>
            <TableHead>Date/Time</TableHead>
            <TableHead>Notes</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {data.length ? (
            data.map((sample) => {
              const location = PROCESSING_LOCATIONS.find(l => l.id === sample.locationId);
              const statusColor = sample.status === 'PASS' ? 'text-green-600' : 
                                sample.status === 'WARNING' ? 'text-yellow-600' : 'text-red-600';
              
              return (
                <TableRow key={sample.id}>
                  <TableCell className="font-medium">{sample.sampleId}</TableCell>
                  <TableCell>{location?.name || sample.locationId}</TableCell>
                  <TableCell>{location?.product || ""}</TableCell>
                  <TableCell>{sample.qualityValue}{location?.unit || ''}</TableCell>
                  <TableCell>
                    <Badge 
                      variant={sample.status === 'PASS' ? 'default' : 
                               sample.status === 'WARNING' ? 'secondary' : 'destructive'}
                    >
                      {sample.status}
                    </Badge>
                  </TableCell>
                  <TableCell>{sample.operator}</TableCell>
                  <TableCell>{new Date(sample.timestamp).toLocaleString()}</TableCell>
                  <TableCell>{sample.notes || "-"}</TableCell>
                </TableRow>
              );
            })
          ) : (
            <TableRow>
              <TableCell colSpan={8} className="h-24 text-center">
                No results.
              </TableCell>
            </TableRow>
          )}
        </TableBody>
      </Table>
    </div>
  );
}
